import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import Buefy from 'buefy'
Vue.use(Vuex)
Vue.use(Buefy)

export default new Vuex.Store({
  state: {
    baseurl: 'https://my.tmdhosting.com/admin/',
    list: []
  },
  mutations: {
    setList(state, value) {
      state.list = value
    },
  },
  actions: {
    getServers(context) {
      axios
        .get("addonmodules.php?module=LPRManager&c=ServerList&json=1")
        .then((response) => {
          if (response) {

            context.commit('setList', response.data)
          }
        })
    }
  },
  modules: {
  }
})
