<template>
  <div class="tile">
    <article class="panel is-primary">
      <p class="panel-heading">
        {{ region[0].region.region }}
        <country-flag :country="region[0].region.country" size="normal" />
      </p>
      <div class="tile is-ancestor">
        <div
          class="tile"
          v-for="(region_item, i) in hostingActiveRegions"
          :key="i"
        >
          <aside
            class="menu"
            v-for="(pool, j) in region_item.pools"
            :key="i + '' + j"
          >
            <p class="menu-label">
              {{ region_item.name }} -
              {{ pool.primary === 1 ? "Primary" : "Backup" }}
            </p>
            <ul class="menu-list">
              <li v-for="(server, s) in pool.servers" :key="s">
                <a>{{ server.server.hostname }}</a>
              </li>
            </ul>
          </aside>
        </div>
      </div>
    </article>
  </div>
</template>

<script>
export default {
  name: "Region",
  props: {
    region: {
      type: Array,
      default: function () {
        return {};
      },
    },
  },
  computed: {
    hostingActiveRegions() { 
      return this.region.filter((i) => 
         i.region.disabled === "0" && i.disabled === "0"
     );
    },
  },
};
</script>

