<template>
  <div class="tile is-ancestor">
    <Region v-for="(item, i) in regions" :region="item" :key="i" />
  </div>
</template>

<script>
import Region from "./Region.vue";
export default {
  components: { Region },
  props: {
    listsid: Array,
  },
  computed: {
    regions() {
      return this.$store.state.list.filter((i) => this.listsid.includes(i[0].region_id));
    },
  },
};
</script>

